import sinon from 'sinon';
import {mapStateToProps} from "../../../src/components/app-header";
import { IStore } from "../../../src/redux/store/types";

import * as selectors from "../../../src/redux/selectors/selectors";

const getToDoCountStub = sinon.stub().returns(10);
sinon.replace(selectors, 'getToDoCount', getToDoCountStub);

const getDoneCountStub = sinon.stub().returns(100);
sinon.replace(selectors, 'getDoneCount', getDoneCountStub);

describe('src/components/AppHeader', () => {

    beforeEach(() => {
        sinon.resetHistory();
    });

    it('mapStateToProps', () => {
        //Given
        const store = {} as IStore;

        const expected = {
            toDo: 10,
            done: 100
        };

        //When
        const actual = mapStateToProps(store);

        //Then
        expect(actual).toEqual(expected);
    })
});